//
//  Flatpickr
//

$flatpickr-padding-y: 0.5rem;
$flatpickr-padding-x: 1rem;
$flatpickr-width: 315px;
$flatpickr-day-height: 36px;
$flatpickr-time-height: 46px;

// Container
.flatpickr-calendar {
  font-family: inherit;
  border: 0;
  box-shadow: $dropdown-box-shadow;
  border-radius: $dropdown-border-radius;
  background-color: $body-bg;

  // Remow arrows
  &:before,
  &:after {
    display: none;
  }
}

// Months container
.flatpickr-months {
  padding: 0 $flatpickr-padding-x;
  padding-top: $flatpickr-padding-y;
}

// Inner container
.flatpickr-innerContainer {
  padding: $flatpickr-padding-y $flatpickr-padding-x;
}

// Day containers
.flatpickr-days,
.dayContainer {
  width: 100% !important;
  min-width: 100% !important;
  max-width: 100% !important;
}

// Months
.flatpickr-months {
  .flatpickr-month {
    background: transparent;
    color: $gray-600;
    fill: $gray-600;
    height: 46px;
  }

  .flatpickr-prev-month,
  .flatpickr-next-month {
    display: flex;
    align-items: center;
    justify-content: center;
    @include border-radius($border-radius);
    top: 1rem;

    svg {
      fill: $gray-500;
      height: 13px;
      width: 13px;
    }

    &:hover {
      background: $gray-100;

      svg {
        fill: $gray-700;
      }
    }
  }

  .flatpickr-prev-month.flatpickr-prev-month,
  .flatpickr-next-month.flatpickr-prev-month {
    /*rtl:begin:ignore*/
    left: $flatpickr-padding-x;
    /*rtl:end:ignore*/
  }

  .flatpickr-prev-month.flatpickr-next-month,
  .flatpickr-next-month.flatpickr-next-month {
    /*rtl:begin:ignore*/
    right: $flatpickr-padding-x;
    /*rtl:end:ignore*/
  }
}

// Month selector
.flatpickr-current-month {
  font-weight: $font-weight-bold;
  color: inherit;

  .numInputWrapper {
    @include border-radius($border-radius);
    width: 65px;

    span {
      &.arrowUp {
        @include border-top-end-radius($border-radius);
      }

      &.arrowDown {
        @include border-bottom-end-radius($border-radius);
      }
    }
  }

  .flatpickr-monthDropdown-months {
    border: 0 !important;
    background-color: $body-bg;
    font-size: 1rem;
    color: $gray-700;
    font-weight: $font-weight-bold;
    padding: 0.5rem 0.75rem;
    margin-right: 0.5rem;
    outline: none !important;
    @include border-radius($border-radius);
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;

    &:hover {
      background: $gray-100;
    }

    .flatpickr-monthDropdown-month {
      font-size: 1rem;
      color: $gray-700;
      font-weight: $font-weight-bold;
      background-color: transparent;
      outline: none;
      padding: 0;
    }
  }

  span.cur-month {
    color: $gray-700;
    font-size: 1rem;
    font-weight: $font-weight-bold;

    &:hover {
      background: $gray-100;
    }
  }

  input.cur-year {
    color: $gray-700;
    font-size: 1.1rem;
    padding: 0.5rem 0.75rem;
    font-weight: $font-weight-bold;
    outline: 0 !important;
  }
}

// Week days
span.flatpickr-weekday {
  color: $gray-800;
  font-size: 1rem;
  font-weight: $font-weight-bolder;
}

// Time
.flatpickr-time {
  @include border-bottom-radius($border-radius);

  .flatpickr-calendar.hasTime & {
    height: height;
    line-height: height;
    max-height: height;
    border-top: 1px solid $gray-100;
  }

  .numInputWrapper {
    height: height;
  }

  .flatpickr-am-pm {
    color: $gray-700;
    font-size: 1rem;
    font-weight: $font-weight-bold;
  }

  input {
    &.flatpickr-hour,
    &.flatpickr-minute {
      color: $gray-700;
      font-size: 1rem;
      font-weight: $font-weight-bold;
    }
  }

  input:hover,
  .flatpickr-am-pm:hover,
  input:focus,
  .flatpickr-am-pm:focus {
    background: transparent;
  }
}

// Number input
.numInputWrapper {
  span {
    border-left: 0 !important;
    border-top: 0 !important;
    border-bottom: 0 !important;
    border-right: 0 !important;

    &:hover {
      background: transparent !important;
    }

    &:after {
      top: 50% !important;
      transform: translateY(-50%);
    }

    &.arrowUp {
      &:after {
        border-bottom-color: $gray-500 !important;
      }

      &:hover {
        &:after {
          border-bottom-color: $gray-700 !important;
        }
      }
    }

    &.arrowDown {
      &:after {
        border-top-color: $gray-500 !important;
      }

      &:hover {
        &:after {
          border-top-color: $gray-700 !important;
        }
      }
    }
  }

  &:hover {
    background: transparent;
  }
}

// Day
.flatpickr-day {
  font-size: 1rem;
  @include border-radius($border-radius);
  box-shadow: none !important;
  height: $flatpickr-day-height;
  max-width: $flatpickr-day-height;
  line-height: $flatpickr-day-height;
  color: $gray-600;

  &.inRange,
  &.prevMonthDay.inRange,
  &.nextMonthDay.inRange,
  &.today.inRange,
  &.prevMonthDay.today.inRange,
  &.nextMonthDay.today.inRange,
  &:hover,
  &.prevMonthDay:hover,
  &.nextMonthDay:hover,
  &:focus,
  &.prevMonthDay:focus,
  &.nextMonthDay:focus {
    cursor: pointer;
    outline: 0;
    background: $component-hover-bg;
    color: $component-hover-color;
    border-color: transparent;
  }

  // Today
  &.today {
    background: $gray-100;
    color: $gray-600;
    border-color: transparent;

    &:hover,
    &:focus {
      border-color: transparent;
      background: $gray-200;
      color: $gray-700;
    }
  }

  // Selected
  &.selected,
  &.startRange,
  &.endRange,
  &.selected.inRange,
  &.startRange.inRange,
  &.endRange.inRange,
  &.selected:focus,
  &.startRange:focus,
  &.endRange:focus,
  &.selected:hover,
  &.startRange:hover,
  &.endRange:hover,
  &.selected.prevMonthDay,
  &.startRange.prevMonthDay,
  &.endRange.prevMonthDay,
  &.selected.nextMonthDay,
  &.startRange.nextMonthDay,
  &.endRange.nextMonthDay {
    background: $component-active-bg;
    color: $component-active-color;
    border-color: transparent;
  }

  &.inRange,
  &.prevMonthDay.inRange,
  &.nextMonthDay.inRange,
  &.today.inRange,
  &.prevMonthDay.today.inRange,
  &.nextMonthDay.today.inRange,
  &:hover,
  &.prevMonthDay:hover,
  &.nextMonthDay:hover,
  &:focus,
  &.prevMonthDay:focus,
  &.nextMonthDay:focus {
    cursor: pointer;
    outline: 0;
    background: $component-hover-bg;
    color: $component-hover-color;
    border-color: transparent;
  }

  // Today
  &.today {
    border-color: transparent;
  }

  &.today:hover,
  &.today:focus {
    border-color: transparent;
    background: $gray-100;
    color: $gray-600;
  }

  // Selected
  &.selected,
  &.startRange,
  &.endRange,
  &.selected.inRange,
  &.startRange.inRange,
  &.endRange.inRange,
  &.selected:focus,
  &.startRange:focus,
  &.endRange:focus,
  &.selected:hover,
  &.startRange:hover,
  &.endRange:hover,
  &.selected.prevMonthDay,
  &.startRange.prevMonthDay,
  &.endRange.prevMonthDay,
  &.selected.nextMonthDay,
  &.startRange.nextMonthDay,
  &.endRange.nextMonthDay {
    background: $component-active-bg;
    color: $component-active-color;
    border-color: transparent;
  }

  // Disabled
  &.flatpickr-disabled,
  &.flatpickr-disabled:hover,
  &.prevMonthDay,
  &.nextMonthDay,
  &.notAllowed,
  &.notAllowed.prevMonthDay,
  &.notAllowed.nextMonthDay {
    color: $gray-400;
    background: transparent;
    border-color: transparent;
  }
  &.flatpickr-disabled,
  &.flatpickr-disabled:hover {
    cursor: not-allowed;
    color: $gray-400;
  }
}
.flatpickr-input[readonly] {
  width: 100%;
}
